@font-face {
  font-family: 'favorit-italic';
  src: url('fonts/Favorit_Regular-Italic.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'favorit';
  src: url('fonts/Favorit_Regular.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'GothamRnd-Book';
  src: url('fonts/gothamrounded-book-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'GothamRnd-Bold';
  src: url('fonts/gothamrounded-bold-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'HelveticaNeue';
  src: url('fonts/HelveticaNeueCyr-Roman.woff') format('woff');
  font-weight: bold;
}

/* ============ FONT COLORS BEGIN ============== */
html {
  --par-font-color: #444444;
  --tag-font-color: #F7F7F7;
  --light-gray-font-color: #938F8F; /* not selected metatags, menu options etc. */
  --dark-gray-font-color: #555555; /* selected metatags, link hovers, column title etc. */
  --card-title-font-color: #4E4E4E; /* quiz card title */
  --light-cream-color: rgb(247, 241, 234);
}
/* ============ FONT COLORS END ============== */

.everything {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 400%;
}

.logo-div {
  position: sticky;
  position: -webkit-sticky;
  z-index: 29;
}

.intact-logo {
  text-align: center;
  font-family: 'GothamRnd-Bold';
  text-transform: uppercase;
  color: var(--light-cream-color);
  letter-spacing: 0.18px;
  margin: auto;
  z-index: 100;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

.parallax-container {
  margin-top: -58px;
  height: 400vh;
  overflow: hidden;
  top: -58px;
}

.landing-slide {
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
  height: 58px;
  background: var(--light-cream-color);
  z-index: 10;
  /*box-shadow: 0 -4px 14px 3px rgba(0,0,0,0.02);*/
  min-height: 58px;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
}

.topbar-spotlight-wrapper {
    position: absolute;
    top: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
    z-index: 2000;
}

.topbar-spotlight {
    z-index: 2;
    height: auto;
    position: absolute;
    overflow: hidden;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: block;
    pointer-events: auto;
}

.landing-slide, .first-slide, .second-slide, .third-slide, .fourth-slide {
  position: relative;
  height: 100vh;
  width: 100vw;
}
.first-slide {
  padding-top: 58px;
}

.processing {
  /*background: #ddd4c1;*/
  height: 100vh;
  width: 100vw;
  background: rgb(247, 241, 234);
  /*z-index: 20;*/
}

.App-logo, .logo {
  text-align: center;
  font-family: 'GothamRnd-Bold';
  text-transform: uppercase;
  font-size: 20px;
  color: rgb(247, 241, 234);
  letter-spacing: 0.18px;
  margin: auto;
}

.aligner {
  margin-top: 58px;
  position: absolute;
  width: 100%;
  height: calc(100% - 58px);
}

.aligned {
  margin: auto;
}

.flex-grow {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.par, .role {
  /*text-align: justify;*/
  float: left;
  font-family: 'favorit';
  font-size: 38px;
  color: black;
  letter-spacing: 0;
  line-height: 48px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}


.italic {
  font-family: 'favorit-italic';
}

.selected-role {
  text-decoration: line-through;
}

.aligner-box {
  height: 100%;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.central-par {
  padding-top: 8%;
}

.second-page-par, .central-par {
  max-width: 100%;
  padding: 12px;
  font-size: 20px;
}

.line {
  border-bottom: 1px solid #979797;
}

@media only screen and (min-width: 40em) {
  .top-par {
    padding-top: 28%;
  }

  .top-par-second {
    padding-top: 24%;
  }
}

.Fullpage {
  margin-top: 100vh;
}

.landing-spotlight {
  border: 100px;
  position: absolute;
  width: 400px;
  height: 400px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: white;
  -moz-box-shadow: 0 0 150px 110px white;
  -webkit-box-shadow: 0 0 150px 100px white;
  -o-box-shadow: 0 0 150px 110px white;
}

.down-arrow {
  height: 32px;
  width: 32px;
  opacity: 0.16;
  z-index: 10000;
  padding-bottom: 12px;
}



